  text-align: center;
  box-shadow: 1px 1px 20px #001f86;
  border: solid 1px #003479;
  right: 40px;
  bottom: 40px;
  font-size: 20px;
  background: #00196b;
  color: white;
}

.section_wordmanager .disable_btn_wrap {
  position: absolute;
  top: 6px;
}

.section_wordmanager .disable_btn_wrap .filter_group .filter_label {
  width: 49px;
  height: 20px;
  border-radius: 20px;
  background: #707070;
  position: absolute;
  border: 1px solid #70707083;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.section_wordmanager .disable_btn_wrap .filter_group .filter_circle {
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: -webkit-gradient(linear, left top, left bottom, from(#ffffff), to(#FCFCFC));
  background: -webkit-linear-gradient(#ffffff, #FCFCFC);
  background: linear-gradient(#ffffff, #FCFCFC);
  border: solid 1px #DEDFDF;
  box-shadow: 0px 1px 3px #0000001A;
  position: absolute;
  right: -23px;
  top: 1px;
  -webkit-transition: -webkit-transform 0.3s ease-out;
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.section_wordmanager .disable_btn_wrap .filter_group input {
  width: 1px;
  height: 1px;
  opacity: 0;
  position: absolute;
}

.section_wordmanager .disable_btn_wrap .filter_group input:checked + .filter_label {
  background: #2FC21D;
}

.section_wordmanager .disable_btn_wrap .filter_group input:checked ~ .filter_circle {
  -webkit-transform: translateX(25px);
          transform: translateX(25px);
}
